<template>
	<view class="container">
		<view class="item" v-for="g in collectList" :key="g.goods_id" @click="toDetail(g.goods.id)">
			<view class="left">
				<image :src="g.goods.image"></image>
				<view class="info">
					<view class="text">
						<view class="text1">{{g.goods.title}}</view>
						<view class="text2">{{g.goods.describe}}</view>
					</view>
					<view class="price">￥{{g.goods.price.toFixed(2)}}/份</view>
				</view>
			</view>
			<uni-icons type="trash-filled" size="30" color="#ff557f" @click="cancelCollectGoods(g.goods_id)"></uni-icons>
		</view>
	</view>
</template>

<script>
	import {
		mapState,
		mapActions
	} from 'vuex'
	export default{
		data(){
			return{
				
			}
		},
		onLoad(){
			
		},
		computed:{
			...mapState(['collectList'])
		},
		methods:{
			...mapActions(['cancelCollectGoods']),
			toDetail(id){ //跳转商品详情
				uni.navigateTo({
					url:'/pages/goods/detail?goodsId='+id
				})
			},
		}
	}
</script>

<style lang="scss">
	.container{
		padding: 20upx;
		.item{
			display: flex;
			align-items: center;
			justify-content: space-between;
			background-color: #fff;
			border-radius: 10upx;
			margin-bottom: 10upx;
			padding: 30upx 40upx 30upx 20upx;
			.left{
				display: flex;
				align-items: center;
				justify-content: flex-start;
				image{
					width: 250upx;
					height: 250upx;
				}
				.info{
					display: flex;
					flex-direction: column;
					justify-content: space-between;
					align-items: flex-start;
					height: 250upx;
					padding-left: 20upx;
					.text{
						.text1{
							font-size: 30upx;
							font-weight: bold;
						}
						.text2{
							font-size: 24upx;
							color: #bdbdbd;
							margin-top: 10upx;
						}
					}
					.price{
						color: #ff557f;
					}
				}
			}
		}
	}
</style>